.attempt-wrapper{
    display: flex;
    flex-direction: row;

    .char-box{
        width: 50px;
        height: 50px;
        // border: 1px solid white;
        color: black;
        font-weight: 900;
        font-size: 20px;
        margin: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-transform: uppercase;
    }

    .char-box.grey{
        background-color: rgb(180, 180, 180);
    }
    .char-box.yellow{
        background-color: yellow;
    }
    .char-box.green{
        background-color: rgb(0, 255, 47);
    }
    .char-box.purple{
        background-color: purple;
    }
    .char-box.red{
        background-color: rgb(128, 0, 0);
    }
}   